<template>
  <v-container fluid style="position: center">
    <ul class="vova" >
      <li v-for="Card in Cards" :key = "Card.id">
        <v-card
            class="mx-auto"
            max-width="344"
        >
          <v-img
              :src="Card.linkimage"
              height="200px"
          ></v-img>

          <v-card-title>
            {{ Card.name }}
          </v-card-title>

          <v-card-subtitle>
          </v-card-subtitle>

          <v-card-actions>
            <v-rating
                v-model="Card.rating"
                icon-label="custom icon label text {0} of {1}"
                color="green"
                background-color="green"
            ></v-rating>


            <v-spacer></v-spacer>
            {{ Card.like }}
            <v-btn
                icon
                color="green"
                @click="Card.like++"
            >
              <v-icon>mdi-heart</v-icon>
            </v-btn>
            <v-btn
                icon
                @click="Card.show = !Card.show"
            >
              <v-icon>{{ Card.show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
            </v-btn>
          </v-card-actions>

          <v-expand-transition>
            <div v-show="Card.show">
              <v-divider></v-divider>

              <v-card-text>
                {{Card.textcard}}
              </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
      </li>
    </ul>
    <slot name="text"></slot>
  </v-container>



</template>

<script>
import store from '@/Store/index.js';
export default {
  data: () => ({
    Cards:[
      {id:1,textcard:store.state.ArrayCurrent[0],show:false,linkimage:store.state.ArrayLinkCurrent[0],name:store.state.ArrayNameCurrent[0],rating: 0,like:0},
      {id:2,textcard:store.state.ArrayCurrent[1],show:false,linkimage:store.state.ArrayLinkCurrent[1],name:store.state.ArrayNameCurrent[1],rating: 0,like:0},
      {id:3,textcard:store.state.ArrayCurrent[2],show:false,linkimage:store.state.ArrayLinkCurrent[2],name:store.state.ArrayNameCurrent[2],rating: 0,like:0},
    ]

    //show: false,
  })
}
</script>

<style scoped>
.vova{
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  list-style-type:none;
  row-gap:10px;
  column-gap: 100px;

}
</style>
