<template>
  <div>
    <v-tabs
        fixed-tabs
        background-color="green"
        dark
    >
      <v-tab v-on:click="getVova(1)" v-text="$t('PageName1')">

      </v-tab>
      <v-tab v-on:click="getVova(2)" v-text="$t('PageName2')">

      </v-tab>
      <v-tab v-on:click="getVova(3)" v-text="$t('PageName3')">

      </v-tab>
      <v-tab v-on:click="getVova(4)" v-text="$t('PageName4')">
      </v-tab>
    </v-tabs>
    <div>
      <v-btn @click="SetLocate('en')" color = "brown"> En </v-btn>
      <v-btn @click="SetLocate('ru')" color = "brown"> Ru </v-btn>
    </div>

  </div>

</template>

<script>
import store from '@/Store/index.js';
import router from "@/router";
import {i18n} from "@/i18n";

export default {
  methods:{
    getVova(int){
      if(int === 1)
      {
        store.commit("Page1")
        router.push("/")
      }
      if(int === 2)
      {
        store.commit("Page2")
        router.push("/Pages/VDogs")
      }
      if(int === 3)
      {

        store.commit("Page3")
        router.push("/Pages/VHamsters")
      }
      if(int === 4)
      {
        store.commit("Page4")
        router.push("/Pages/VParrots")
      }
    },
    SetLocate(locale){
      this.$i18n.locale = locale
      store.state.ArrayName = i18n.t('ArrayNameAnimals')
      store.state.Array = i18n.t('ArrayTextAnimals')
    }
  }
}
</script>

