import Vue from 'vue'
import VueRouter from "vue-router";
import VCats from "@/Pages/VCats";
import VDogs from "@/Pages/VDogs";
import VHamsters from "@/Pages/VHamsters";
import VParrot from "@/Pages/VParrot";
Vue.use(VueRouter)

const routes  = [
    {
        path: '/',
        component: VCats,
        name:"page1"
    },
    {
        path: '/Pages/VDogs',
        component: VDogs,
        name:"page2"
    },
    {
        path: '/Pages/VHamsters',
        component: VHamsters,
        name:"page3"
    },
    {
        path: '/Pages/VParrots',
        component: VParrot,
        name:"page4"
    }
]
export default new VueRouter({
    mode: 'hash',
    routes
})
